import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';
import uploadImage from '@/components/common/upload-image.vue';
export default {
	components:{
		uniNavBar,
		uploadImage
	},
	data() { 
		return {
			content:"",
			imageList: [],
			//记录是否已经显示返回
			showBack: false,
			
		}
	},
	//监听页面返回操作
	onBackPress(){
		if( (this.content !== '' || this.imageList.length >0) && (!this.showBack)){
			this.showBack = true
			uni.showModal({
				title: '',
				content: '是否要保存为草稿?',
				showCancel: true,
				cancelText: '取消保存',
				confirmText: '保存',
				success: res => {
					//点击确认，保存草稿
					if(res.confirm){
						this.saveContent()
					}else if(res.cancel){
						this.deleteContent()
					}
					//手动返回
					uni.navigateBack({
						delta: 1
					});
					
				}
			});
			return true
			
		}
		
	},
	//加载页面，展示草稿内容
	onLoad(){
		uni.getStorage({
			key: 'add-input',
			success: res => {
				if(res){
					let ret = JSON.parse(res.data)
					this.content = ret.content
					this.imageList = ret.imageList
				}

			}
		})
	},
	computed: {
		//控制添加图片控件的显示
		show() {
			return this.imageList.length > 0
		}
	},
	methods: {
		//返回页面
		back(){
			uni.navigateBack({
				delta: 1
			});
		},
		//添加或者删除图片触达发
		changeImage(e){
			this.imageList = e
		},
		//保存草稿
		saveContent(){
			uni.setStorage({
				key: 'add-input',
				data: JSON.stringify({
					content: this.content,
					imageList: this.imageList
				})
			})
			
		},
		//删除草稿
		deleteContent(){
			uni.removeStorage({
				key : 'add-input',
			})
		},
		//底部图标点击事件
		iconClickEvent(type){
			switch (type){
				case 'uploadImage':
				this.$refs.uploadImage.chooseImage()
					break;
				default:
					break;
			}
		}
		
	}
}
